import React from 'react';


import './sign-up.styles.scss';
import { auth, createUserProfileDocument } from '../../firebase/firebase.utils';
import FormInput from '../Forminput/FormInput.component.jsx';

import CustomButton from '../custombutton/CustomButton.component.jsx';

class SignUp extends React.Component {
    constructor() {
        super();
        this.state = {
            email: '',
            password: '',
            displayName: '',
            confirmPassword: ''
        }
    }
    handleSubmit = async (event) => {
        event.preventDefault();
        const { displayName, email, password, confirmPassword } = this.state;
        if (password !== confirmPassword) {
            alert("password didn't match");
            return;
        }
        try {
            const { user } = await auth.createUserWithEmailAndPassword(email, password).then(function (user) {
                console.log('uid', user.uid)

                //Here if you want you can sign in the user
            }).catch(function (error) {
                console.info(error);
            });
            await createUserProfileDocument(user, { displayName });
            this.setState({
                email: '',
                password: '',
                displayName: '',
                confirmPassword: ''
            })
        }
        catch (error) {
            console.error(error);
        }

    }
    handleChange = (event) => {
        event.preventDefault();
        const { value, name } = event.target;
        this.setState({ [name]: value });
    }
    render() {
        return (
            <div className="sign-up">
                <h2 className="title">I do not have account</h2>
                <span>Sign Up with email and password</span>
                <form className='sign-up-form' onSubmit={this.handleSubmit}>
                    <FormInput type="text"
                        name="displayName"
                        required
                        value={this.state.displayName}
                        handleChange={this.handleChange}
                        label="Display Name"
                    />
                    <FormInput type="email"
                        name="email"
                        required
                        value={this.state.email}
                        handleChange={this.handleChange}
                        label="Email"
                    />
                    <FormInput type="password"
                        name="password"
                        required
                        value={this.state.password}
                        handleChange={this.handleChange}
                        label="Password"
                    />
                    <FormInput type="password"
                        name="confirmPassword"
                        required
                        value={this.state.confirmPassword}
                        handleChange={this.handleChange}
                        label="Confirm Passord"
                    />
                    <CustomButton type="submit">SIGN UP</CustomButton>
                </form>
            </div>
        )
    }
}

export default SignUp;