import React from 'react';


import './sign-in.styles.scss';
import FormInput from '../Forminput/FormInput.component.jsx';
import CustomButton from '../custombutton/CustomButton.component.jsx';

import { auth, SignInWithGoogle } from '../../firebase/firebase.utils';


class SignIn extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            password: ''
        }

    }
    handleSubmit = async (event) => {
        event.preventDefault();
        const { email, password } = this.state;
        try {
            await auth.signInWithEmailAndPassword(email, password);
            this.setState({ email: '', password: '' });
        } catch (error) {
            console.info(error);
        }
    }

    handleChange = (event) => {
        const { value, name } = event.target;
        this.setState({ [name]: value });
    }
    render() {
        return (
            <div className="sign-in">
                <h2>I Allready have an account</h2>
                <span>Please sign In Here</span>
                <form onSubmit={this.handleSubmit}>
                    <FormInput handleChange={this.handleChange} label="email" name="email" type="email" value={this.state.email} required />
                    <FormInput handleChange={this.handleChange} label="password" name="password" type="password" value={this.state.password} required />
                    <div className="buttons">
                        <CustomButton type="submit">SIGN IN</CustomButton>
                        <CustomButton onClick={SignInWithGoogle} isGoogleSignIn>{' '}Sign In With Google{' '}</CustomButton>
                    </div>
                </form>
            </div>
        )

    }
}



export default SignIn;