import { combineReducers } from 'redux';
import userReducer from '../user-reducer/user-reducer.jsx';
import toggleCart from '../cart/cart-reducer.component.jsx';
import DirectoryReducer from '../directory/directory.reducer.js';
import ShopReducer from "../shop/shop.reducer.js";



import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";

const persistConfing = {
    'key': 'root',
    storage,
    whitelist: ['cart']
}

export const rootReducer = combineReducers({
    user: userReducer,
    cart: toggleCart,
    directory: DirectoryReducer,
    shop: ShopReducer

})

export default persistReducer(persistConfing, rootReducer)
