import React from 'react';


import './collection-item.styles.scss';
import CustomButton from '../custombutton/CustomButton.component.jsx';
import { connect } from 'react-redux';

import { addItemAction } from '../../redux/cart/cart.action';



const CollectionItem = ({ item, addItem }) => {
    const { title, id, imageUrl, price, name } = item;
    return (
        <div className="collection-item">
            <div style={{ backgroundImage: `url(${imageUrl})` }} className="image">

            </div>
            <div className="collection-footer">
                <span className="name">{name}</span>
                <span className="price">{price}</span>
            </div>
            <CustomButton inverted="inverted" onClick={() => addItem(item)}>Add To Cart</CustomButton>
        </div>
    )
}



const mapDispatchToProps = dispatch => ({
    addItem: item => dispatch(addItemAction(item))
})


export default connect(null, mapDispatchToProps)(CollectionItem);