import React from 'react';
import './checkoutpage.styles.scss';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { selectCartItems, selectCartTotal } from '../../redux/cart/cart.selectors'
import CheckOutItem from '../../components/checkout-item/checkout-item.component';

import StripeCheckoutButton from "../../components/stripe-button/stripe-button.component.jsx";




const CheckOutpage = ({ cartItems, Total }) => {
    return (
        <div className="checkout-page">
            <div className="checkout-header">


                <div className="header-block">
                    <span>Product</span>
                </div>
                <div className="header-block">
                    <span>Description</span>
                </div>
                <div className="header-block">
                    <span>Quantity</span>
                </div>
                <div className="header-block">
                    <span>Price</span>
                </div>
                <div className="header-block">
                    <span>Remove</span>
                </div>
            </div>
            {
                cartItems.map(cartItem => <CheckOutItem cartItem={cartItem} />)
            }
            <div className="total">
                <span>TOTAL: ${Total}</span>

            </div>
            <div className="test-warning">
                *please use following credit card for payment*
            <br />
            4242 4242 4242 4242 EXP: 10/20 CVV:123
            </div>
            <StripeCheckoutButton price={Total} />
        </div>
    )
}

const mapStateToProps = createStructuredSelector({
    cartItems: selectCartItems,
    Total: selectCartTotal
})

export default connect(mapStateToProps)(CheckOutpage);