import CartToggleActionTypes from '../cart/cart.actiontypes';


export const CartToggleAction = () => ({
    type: CartToggleActionTypes.CART_TOGGLE
})

export const addItemAction = (item) => ({
    type: CartToggleActionTypes.ADD_ITEM,
    payload: item
})

export const clearItemFromCart = (item) => ({
    type: CartToggleActionTypes.CLEAR_ITEM_FROM_CART,
    payload: item
})


export const removeItem = (item) => ({
    type: CartToggleActionTypes.REMOVE_ITEM,
    payload: item
})
export default CartToggleAction;