import React from 'react';

import StripeCheckout from "react-stripe-checkout";

const StripeCheckoutButton = ({ price }) => {
    const PriceforStripe = price * 100;






    const onToken = (token) => {
        console.log(token);
        alert("Payment Successful");
    }




    const PublishableKey = "pk_test_51H4mN7CT7F79KLPIYPl35t5cPYteJERNpXVEG1ForEgPsIyRv6xf49PzDesP5Ox7ToE7ixZPhzUmmehd6wsWpyEm001NLhUuJF";


    return (
        <StripeCheckout
            label="Pay Now"
            name="ganesh stores"
            billingAddress
            shippingAddress
            image=''
            description={`Your Total is : $${price}`}
            amount={PriceforStripe}
            panelLabel="Pay Now"
            token={onToken}
            stripeKey={PublishableKey}
        />
    )

}

export default StripeCheckoutButton;