import React from 'react';


import './checkout.item.styles.scss';
import { connect } from 'react-redux';
import { clearItemFromCart, removeItem, addItemAction } from '../../redux/cart/cart.action';

import { createStructuredSelector } from "reselect";


const CheckOutItem = ({ cartItem, dispatch }) => {
    const { name, imageUrl, price, quantity } = cartItem;
    return (

        <div className="checkout-item">
            <div className="image-container">

                <img src={imageUrl} alt="img" />
            </div>
            <span className="name">{name}</span>
            <span className="quantity">
                <div className="arrow" onClick={() => dispatch(removeItem(cartItem))}>
                    &#10094;
                </div>
                <span className="value">
                    {quantity}
                </span>
                <div className="arrow" onClick={() => dispatch(addItemAction(cartItem))}>
                    &#10095;
                </div>

            </span>
            <span className="price">{price}</span>
            <div className="remove-button" onClick={() => dispatch(clearItemFromCart(cartItem))}>&#10005;</div>

        </div>

    )
}




export default connect()(CheckOutItem);