import cartActiontypes, { CartToggleActionTypes } from '../cart/cart.actiontypes';
import addItemTocart from '../cart/cart.utils.js';
import { removeItemFromCart } from '../cart/cart.utils.js';
const INTITIAL_STATE = {
    hidden: true,
    cartItems: []
}


const toggleCart = (state = INTITIAL_STATE, action) => {
    switch (action.type) {
        case cartActiontypes.CART_TOGGLE:
            return {
                ...state,
                hidden: !state.hidden
            }
        case cartActiontypes.ADD_ITEM:
            return {
                ...state,
                cartItems: addItemTocart(state.cartItems, action.payload)
            }
        case cartActiontypes.CLEAR_ITEM_FROM_CART:
            return {
                ...state,
                cartItems: state.cartItems.filter((item) => item.id !== action.payload.id)
            }
        case cartActiontypes.REMOVE_ITEM:
            return {
                ...state,
                cartItems: removeItemFromCart(state.cartItems, action.payload)
            }
        default:
            return state;
    }

}
export default toggleCart;